*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 
}

body{
  font-family: 'Montserrat',sans-serif;
  overflow-x: hidden;
}


/* ----------------- Header section----------------------- */
.header{
    background-image: url('img/temp.jpg');
    background-size:cover;
    width: auto;
    max-height:130rem ;
    
}
div.fixed {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 0px 30px 40px;
  cursor: pointer;
  text-align: center;
  width:9.2rem;
  text-decoration: none;
  font-weight: 700;
  border-radius: 15px 50px 30px;
  font-size: 1.4rem; 
  padding: .6rem  1rem;
  background-color:rgb(44, 150, 44);
}

.callnow{
  border-radius: 25px;
  padding: .5rem .7rem ;
  background-color:rgb(44, 150, 44);
}

.fixed a{
  color:#fff;
}

.fixed a:hover{
  text-decoration: none;
}


.navbar{
  display: flex;
  align-items: center;
  padding: 20px;
}

.logo img{
  width: 150px;
}

nav{
  flex:1;
  text-align: right;
}


nav ul{
  display: inline-block;
  list-style-type: none;
}

nav ul li{
  display: inline-block;
  margin: 25px;
}

nav ul li a{
  color: #fff;
  font-weight: 700;
}

nav ul li a:hover{
  text-decoration: none;
  color:cornflowerblue;
 
}


 .container{
  max-width: 1300px;
  margin: auto;
  padding:0 25px;
}

 .row{
  display: flex;
}

div.colum-4{
  flex-basis: 30rem;
  justify-content: space-around;
  align-items: flex-start;
  padding:14rem 0px;
  
}

.colum-4 h1{
  font-size: 3.5rem;
  line-height: 60px;
  margin: 20px 0;
  font-weight: 800;
  color: #D5D5C2;
  font-size: 5rem;
}

.colum-4 h3{
  font-size: 2.2rem;
  font-weight: 800;
}

.colum-4 p{
  margin: 1rem;
  font-weight: 500;
  line-height: 1.5rem;
 
}

.NavLink{
  color:white
}



/* -----------------About section--------------------- */

.about {
  display: flex;
  margin: 50px 70px;
}

.about >div{
  margin: 2rem;
}
.about-a {
  flex-basis:50%;

}
 .about-b{
  flex-basis:50%;
 
 }


.about h3{
  color:rgba(37,37,37,0.7);
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.about h2{
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.about p {
  font-size: 1.2rem;
  columns: #8b8b8b;
  font-weight:300;
  text-align: justify;
}

.about__btn{
  margin-top: 20px;
  overflow: hidden;
}

.link__btn {
  cursor: pointer;
  width: 8rem;
  text-decoration: none;
  border: none;
  outline: none;
  color: rgba(0, 0, 0);
  font-weight: 700;
  border-radius: 1.8rem;
  font-size: 1rem; 
  padding: 1rem 1rem;
  color:white;
  background: #D8D7C3;
}


.about__img img{
  width: 100%;
  height: 350px;
  box-shadow: 10px 10px;
}





/* ----- Price -------- */



.small-container{
  max-width: 1000px;
  margin: auto;
  padding:0px 25px;
}


.small-container .col-4{
  flex-basis: 23%;
  padding:10px;
  cursor: pointer;
  margin: 5px;
  margin-bottom: 2rem;
  transition: transform 0.5s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.col-4 img{
  width: 100%;
} 

.small-container h2{
  font-size: 4rem;
}

.title{
  text-align: center;
  margin: 0 auto 70px;
  position: relative;
  line-height: 60px;
  color: #555;
}

.title h4{
  color:#555;
  font-weight: 800;
}

.hdg{
  height: 70px;
}

.item p{
  font-size: 12px;
  color:black
}


.col-4{
  text-align: center;
  justify-content: center;
}

.item h4{
  font-size:16px;
  text-align: center;
  color:black
}

.item :hover{
  text-decoration: none;
}

.rating .fa{
  color:#ff523b
}

.col-4:hover{
  transform: translateY(-5px);
}

.title{
  text-align: center;
  margin: 30px auto 70px;
  position: relative;
  line-height: 60px;
  color: #555;
}

h2.title::after{
  content: '';
  background: #555;
  width: 80px;
  height: 5px;
  border-radius: 50px;
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
}


.p-btn{
  cursor: pointer;
  text-decoration: none;
  border: none;
  outline: none;
  color: rgba(0, 0, 0);
  font-weight: 700;
  border-radius: 1.8rem;
  text-align: center;
  padding: 1rem 1rem;
  background: #D8D7C3;
  margin: 0% 32%;
  padding: 12px;
  cursor: pointer; 
}

/* --------Gallery ----------- */

.title{
  font-size: 4rem;
  cursor: pointer;
}
.gal-1{
  grid-area: myArea;
}

.gallery {
  display: grid;
  grid-template-areas: 'myArea .' 'myArea .';
  grid-gap: 10px;
  padding:0rem 8rem 5rem;
}

.gallery> div {

  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  width: auto;
  height: 20rem;
}


.gallery >.gal-1{
  background-image: url(img/img3.jpg);
  background-size:cover;
  width: auto;
  height: 580px;
}



.gallery >.gal-2{
  background-image: url(img/img1.jpg);
  background-size:cover;
  width: auto;
  height: auto;
}

.gallery >.gal-3{
  background-image: url(img/img2.jpg);
  background-size:cover;
  width: auto;
  height: auto;
}



/* ----------- Footer-------- */
.footer{
  width: 100%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-image: url("./img/temp.jpg");
}
.footer{
  background: #000;
  color: #8a8a8a;
  font-size: 14px;
  padding: 60px 0 20px;
}

.footer p{
  color: #8a8a8a;
}

.footer h3{
  color:#fff;
  margin-bottom: 20px;
}

.footer-col-1,.footer-col-2,.footer-col-3,.footer-col-4{
  min-width: 250px;
  margin-bottom: 20px;
}

.footer-col-1{
  flex-basis: 30%;
}

.footer-col-2{
  flex:1;
  text-align: center;
}

.footer-col-2 img{
  width: 180px;
  margin-bottom:20px
}

.footer-col-3,.footer-col-4{
  flex-basis: 12%;
  text-align: center;
}
div.footer-col-3 a{
  color:#8a8a8a;
}

ul{
  list-style-type: none;
  cursor: pointer;
}

.app-logo{
  margin-top: 20px;
}

.app-logo img{
  width:140px;
}

.footer hr{
  border:none;
  background:#b5b5b5;
  height:1px;
  margin: 20px 0;
}

.copyright{
  text-align:center;
}
.menu-icon{
  width: 28px;
  margin-left: 20px;
  display: none;
}



@media only screen and (max-width:600px){
  *{
    box-sizing: border-box;
  }
  body{
    padding: 0;
    margin: 0;
    overflow-x: hidden;
  }

  div.fixed{
    width:4rem;
    font-size: 13px;
    bottom:0;
    right:0;
    text-overflow: clip;
  }

  div.colum-4{
   padding: 150px 20px 200px;
  }

  ul#MenuItems{
    margin: 0px 0px 0px;
  }
  
  div.navbar{
    padding: 0px 0px;
    display: flex;
   
  }


  nav{
    text-align: left;
  }
  .navbar > div{
    flex-direction: column;
  }

  nav ul li{
    margin-right: 3px;
    font-size: 13px;
  }

  .logo{
   align-self: center;
  } 

  div.colum-4 h1{
    font-size: 75px;  
  }
  
  div.about{
    margin: 0px 0px 0px;
    text-align: center;
  }

  div.about p{
    font-size: 16px;
  }
  div.about-a{
    flex-basis: 90%;
  }

  div.about-b{
    flex-basis: 90%;
  }
 
  div.link__btn{
    position: absolute;
    margin: 0% 15%;
    width: 50%;
    padding: 12px;
    font-size: inherit;
    cursor: pointer;
    text-align: center; 
  }

  .p-btn{
    cursor: pointer;
    text-decoration: none;
    border: none;
    outline: none;
    color: rgba(0, 0, 0);
    font-weight: 700;
    border-radius: 1.8rem;
    text-align: center;
    padding: 1rem 1rem;
    background: #D8D7C3;
    margin: 0% 32%;
    padding: 12px;
    cursor: pointer; 
  }
  
  h2.title{
    font-size: 50px;
    font-weight: 700;
  }

  .small-container{
    padding: 0px;
    margin: 0px;
  }

  .small-container .col-4{
    flex-basis:100%;
    height: fit-content;
  }
  
  .hdg{
    height: 110px;
  }

  #prices >.row{
    align-items: center;
    justify-content: center;
  }
  
  .row{
    text-align: center;
  }

  .img-a{
    display: none;
  }
    .gallery{
    padding: 0px 10px 70px;
  }

  .footer-col-1,.footer-col-2,.footer-col-3,.footer-col-4{
    margin: 0px 70px;
  }
  .footer-col-2,.footer-col-3,.footer-col-4{
    text-align: center;
  }

  .footer h3{
    margin: 0px 0px 10px;
  }
  .footer li{
    margin: 5px 0px 0px;
  }
}